import React from 'react';
import { makeStyles, Theme, useTheme } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import DonationAmountButton from './donation-amount-button';

const useStyles = makeStyles((theme: Theme) => ({
  gridItem: {
    display: 'flex',
    alignContent: 'center',
    justifyContent: 'center',
  },
}));

export interface DonationAmountGridItemProps {
  amount: number;
  selectedDonationAmount: number;
  setDonationAmount: (amount: string) => void;
}

const DonationAmountGridItem = ({ ...props }: DonationAmountGridItemProps) => {
  const theme = useTheme();
  const classes = useStyles(theme);

  return (
    <Grid item xs={5} className={classes.gridItem}>
      <DonationAmountButton {...props}></DonationAmountButton>
    </Grid>
  );
};

export default DonationAmountGridItem;
