import React, { useState, useEffect } from 'react';
import {
  Theme,
  useTheme,
  makeStyles,
  TextField,
  Button,
  Card,
} from '@material-ui/core';
import Divider from '@material-ui/core/Divider';
import SwipeableViews from 'react-swipeable-views';
import * as EmailValidator from 'email-validator';

const useStyles = makeStyles((theme: Theme) => ({
  container: {
    padding: '2rem 1.3125rem',
    margin: 0,
  },
  header: {
    display: 'flex',
    justifyContent: 'space-between',
    marginBottom: '0.5rem',
  },
  donate: {
    fontFamily: '"Colfax Bold", sans-serif',
    fontSize: '1.5rem',
    lineHeight: '2rem',
  },
  cancel: {
    fontFamily: '"Colfax Regular", sans-serif',
    lineHeight: '1.875rem',
    color: theme.palette.primary.main,
  },
  contentContainer: {
    fontFamily: '"Colfax Regular", sans-serif',
    display: 'flex',
    flexDirection: 'column',
    textAlign: 'center',
    fontSize: '1.125rem',
    padding: '2.5rem 1rem 1rem 1rem',
  },
  contentHeader: {
    marginBottom: '1.5rem',
  },
  donationButton: {
    marginTop: '2rem',
    background: theme.palette.primary.main,
    '&:hover': {
      background: theme.palette.primary.light,
    },
    borderRadius: '6.25rem',
    height: '4rem',
    textTransform: 'none',
  },
  donationButtonDisabled: {
    background: 'rgba(130, 42, 144, 0.25)',
  },
  donationButtonText: {
    fontFamily: '"Colfax Medium", sans-serif',
    color: 'white',
    fontSize: '1.125rem',
  },
  tab: {
    background: '#EEEEEE',
    borderRadius: '11.25rem',
    height: '0.3125rem',
    width: '1.75rem',
    margin: 'auto',
    marginBottom: '0.5rem',
  },
}));

export interface BottomSheetProps {
  onClose: () => void;
  onDonate: (email: string) => void;
}

export const BottomSheet = ({ onClose, onDonate }: BottomSheetProps) => {
  const theme = useTheme();
  const classes = useStyles(theme);

  const [sheetIndex, setSheetIndex] = useState(0);
  const [email, setEmail] = useState('');
  const [confirmedEmail, setConfirmedEmail] = useState('');

  const [isEmailValid, setIsEmailValid] = useState(false);

  useEffect(() => {
    if (EmailValidator.validate(email)) {
      setIsEmailValid(true);
    } else {
      setIsEmailValid(false);
    }
  }, [email]);

  return (
    <div className={classes.container}>
      <div className={classes.tab}></div>
      <div className={classes.header}>
        <span className={classes.donate}>Donate</span>
        <span className={classes.cancel} onClick={onClose}>
          Cancel
        </span>
      </div>
      <Divider variant="fullWidth" />
      <SwipeableViews index={sheetIndex} disabled={true}>
        {sheetIndex === 0 && (
          <form>
            <div className={classes.contentContainer}>
              <div className={classes.contentHeader}>
                E-mail my donation tax receipt to:
              </div>
              <TextField
                variant="outlined"
                type="email"
                autoComplete="email"
                onChange={(e) => setEmail(e.target.value)}
                value={email}
              ></TextField>
            </div>

            <Button
              fullWidth={true}
              color="primary"
              disabled={!isEmailValid}
              classes={{
                root: classes.donationButton,
                disabled: classes.donationButtonDisabled,
              }}
              onClick={() => setSheetIndex(1)}
            >
              <span className={classes.donationButtonText}>Next</span>
            </Button>
          </form>
        )}
        {sheetIndex === 1 && (
          <form className={classes.contentContainer}>
            <div className={classes.contentHeader}>
              Please confirm your e-mail address:
            </div>
            <TextField
              variant="outlined"
              type="email"
              value={confirmedEmail}
              onChange={(e) => setConfirmedEmail(e.target.value)}
              autoComplete="email"
            ></TextField>
            <Button
              fullWidth={true}
              color="primary"
              disabled={email !== confirmedEmail}
              classes={{
                root: classes.donationButton,
                disabled: classes.donationButtonDisabled,
              }}
              onClick={() => {
                onDonate(email);
                onClose();
              }}
            >
              <span className={classes.donationButtonText}>Next</span>
            </Button>
          </form>
        )}
      </SwipeableViews>
    </div>
  );
};
