import React, { useState, useEffect } from 'react';
import { Theme, makeStyles, useTheme } from '@material-ui/core/styles';
import ToggleButton from '@material-ui/lab/ToggleButton';

const useStyles = makeStyles((theme: Theme) => ({
  button: {
    margin: '0rem 0rem',
    background: 'white',
    boxShadow:
      '0px 15px 35px rgba(0, 0, 0, 0.08), 0px 0px 5px rgba(0, 0, 0, 0.05)',
    borderRadius: '0.625rem',
    width: '10rem',
    height: '4rem',
  },
  selected: {
    backgroundColor: `#01AFD1 !important`,
  },
  text: {
    fontFamily: '"Colfax Regular", sans-serif',
    color: 'black',
    fontSize: '1.25rem',
  },
}));

export interface DonationAmountButtonProps {
  amount: number;
  selectedDonationAmount: number;
  setDonationAmount: (amount: string) => void;
}

const DonationAmountButton = ({
  amount,
  setDonationAmount,
  selectedDonationAmount,
}: DonationAmountButtonProps) => {
  const theme = useTheme();
  const classes = useStyles(theme);

  const selected = selectedDonationAmount == amount;
  return (
    <ToggleButton
      value={amount}
      className={classes.button}
      selected={selected}
      onClick={() => {
        setDonationAmount(`${amount}`);
      }}
      classes={{
        selected: classes.selected,
      }}
    >
      <span
        className={classes.text}
        style={
          selected
            ? {
                fontFamily: '"Colfax Bold", sans-serif',
                color: 'white',
              }
            : undefined
        }
      >{`$${amount}`}</span>
    </ToggleButton>
  );
};

export default DonationAmountButton;
