import Layout from '../components/layout';
import SEO from '../components/seo';

import PurpleBackground from '../images/purple-background.png';
import {
  Theme,
  Button as MaterialButton,
  useTheme,
  Snackbar,
  SnackbarContent,
  Grid,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import { useState, useEffect } from 'react';
import { BottomSheet } from '../components/bottom-sheet';
import { DefaultApi } from '../api';
import LoadingOverlay from 'react-loading-overlay';
import DonationAmountGridItem from '../components/donation-amount-grid-item';

const useStyles = makeStyles((theme: Theme) => ({
  purpleBackground: {
    backgroundSize: 'cover',
    maxWidth: '100%',
  },
  container: {
    position: 'relative',
    textAlign: 'center',
  },
  primaryContent: {
    position: 'absolute',
    top: '50%',
    left: '50%',
    fontSize: '1.125rem',
    lineHeight: '1.875rem',
    width: '100%',
    transform: 'translate(-50%, -50%)',
    color: 'black',
    fontFamily: '"Colfax Regular", sans-serif',
  },
  singleLine: {
    whiteSpace: 'nowrap',
  },
  donationAmount: {
    fontFamily: '"Colfax Bold", sans-serif',
    fontSize: '1.125rem',
    lineHeight: '1.25rem',
    textAlign: 'center',
    paddingBottom: '1.375rem',
  },
  callToActionText: {
    marginBottom: '0',
  },
  buttonsContainer: {
    marginTop: '1.5rem',
  },
  buttonsGrid: {
    paddingLeft: '12.5%',
    paddingRight: '12.5%',
    justifyContent: 'center',
    [theme.breakpoints.down('md')]: {
      paddingLeft: '7.5%',
      paddingRight: '7.5%',
    },
  },
  buttons: {
    display: 'flex',
    justifyContent: 'center',

    fontSize: '1.125rem',
    lineHeight: '1.875rem',
    width: '100%',

    color: 'white',
    fontFamily: '"Colfax Regular", sans-serif',
  },
  donationBlock: {
    justifyContent: 'center',
    marginTop: '4rem',
    display: 'flex',
  },
  donationTextBlock: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    marginRight: '1rem',
  },
  otherAmountText: {
    fontFamily: '"Colfax Medium", sans-serif',
  },
  maximumAmountText: {
    fontFamily: '"Colfax Regular", sans-serif',
    color: '#999999',
    fontSize: '0.9rem',
  },
  textMedium: {
    fontFamily: '"Colfax Medium", sans-serif',
  },
  donationAmountBox: {
    width: '10rem',
  },
  termsAndConditionsBox: {
    display: 'flex',
    marginTop: '2rem',
    justifyContent: 'center',
  },
  termsAndConditionText: {
    display: 'flex',
    flexDirection: 'column',
  },
  YesIWouldLike: {
    fontFamily: '"Colfax Regular", sans-serif',
    fontSize: '0.875rem',
  },
  termsAndConditions: {
    fontFamily: '"Colfax Regular", sans-serif',
    fontSize: '0.75rem',
    color: theme.palette.primary.main,
  },
  donationButton: {
    marginTop: '2rem',
    background: 'transparent',
    border: `0.125rem solid ${theme.palette.secondary.main}`,
    '&:hover': {
      background: 'transparent',
      border: `0.125rem solid ${theme.palette.secondary.main}`,
    },
    maxWidth: '90%',
    borderRadius: '6.25rem',
    height: '4rem',
    textTransform: 'none',
  },
  donationButtonDisabled: {},
  donationButtonText: {
    fontFamily: '"Colfax Medium", sans-serif',
    color: theme.palette.secondary.main,
    fontSize: '1.125rem',
  },
  form: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  checkBox: {
    marginRight: '1rem',
  },
  errorTextContainer: {
    textAlign: 'center',
    fontFamily: '"Colfax Regular", sans-serif',
    fontSize: '0.875rem',
    lineHeight: '1.25rem',
    background: 'rgba(199, 0, 0, 0.15)',
    padding: '1rem',
    width: '100%',
    color: '#C70000',
    marginTop: '2rem',
  },
  textField: {
    fontSize: '1.75rem',
    fontFamily: '"Colfax Regular", sans-serif',
  },
  dollarSign: {
    fontFamily: '"Colfax Regular", sans-serif',
    fontSize: '1.75rem',
  },
  drawer: {
    borderRadius: ' 1.5625rem 1.5625rem 0rem 0rem',
  },
}));

const IndexPage = () => {
  const theme = useTheme();
  const classes = useStyles(theme);

  const [donationAmount, setDonationAmount] = useState('');
  const [canDonate, setCanDonate] = useState(false);
  const [wantsDonationReceipt] = useState(false);
  const [isDonationAmountTooLarge, setIsDonationAmountTooLarge] = useState(
    false,
  );
  const [isDrawerOpen, setIsDrawerOpen] = useState(false);

  const [isNetworkError, setIsNetworkError] = useState(false);
  const [isNetworkRequest, setIsNetworkRequest] = useState(false);

  useEffect(() => {
    if (
      !isNaN(+donationAmount) &&
      +donationAmount <= 3000.0 &&
      +donationAmount > 0.0
    ) {
      setCanDonate(true);
    } else {
      setCanDonate(false);
    }
    if (!isNaN(+donationAmount) && +donationAmount > 3000.0) {
      setIsDonationAmountTooLarge(true);
    } else {
      setIsDonationAmountTooLarge(false);
    }
  }, [donationAmount]);

  const donate = async (taxReceiptEmail?: string) => {
    const api = new DefaultApi();
    try {
      setIsNetworkRequest(true);
      const { paymentGatewayUrl } = await api
        .requestMoney({
          requestMoneyBody: {
            amount: Number(donationAmount),
          },
        })
        .toPromise();
      window.location.href = paymentGatewayUrl;
    } catch (err) {
      setIsNetworkError(true);
    } finally {
      setIsNetworkRequest(false);
    }
  };

  return (
    <Layout>
      <LoadingOverlay
        active={isNetworkRequest}
        spinner
        text={
          <span
            style={{
              fontFamily: '"Colfax Regular", sans-serif',
            }}
          >
            Processing Donation Request...
          </span>
        }
      >
        <SEO title="Home" />
        <form
          className={classes.form}
          onSubmit={(e) => {
            e.preventDefault();
          }}
        >
          <div className={classes.container}>
            <img
              className={classes.purpleBackground}
              src={PurpleBackground}
            ></img>
            <div className={classes.primaryContent}>
              <div className={classes.callToActionText}>
                <div className={classes.singleLine}>
                  Give a Hoot, donate to the
                </div>
                <div className={classes.singleLine}>Owl Conservatory</div>
                <div className={classes.singleLine}>
                  using INTERAC e-Transfer<sup></sup>
                </div>
              </div>
            </div>
          </div>
          <div className={classes.buttonsContainer}>
            <div className={classes.donationAmount}>Donation Amount</div>
            <Grid className={classes.buttonsGrid} container spacing={3}>
              <Grid item xs={1}></Grid>
              <DonationAmountGridItem
                amount={2}
                setDonationAmount={setDonationAmount}
                selectedDonationAmount={Number(donationAmount)}
              />
              <DonationAmountGridItem
                amount={4}
                setDonationAmount={setDonationAmount}
                selectedDonationAmount={Number(donationAmount)}
              />
              <Grid item xs={1}></Grid>
              <Grid item xs={1}></Grid>
              <DonationAmountGridItem
                amount={6}
                setDonationAmount={setDonationAmount}
                selectedDonationAmount={Number(donationAmount)}
              />
              <Grid item xs={1}></Grid>
            </Grid>
          </div>
          {isDonationAmountTooLarge && (
            <div className={classes.errorTextContainer}>
              <div>Sorry, the maximum donation amount is $3000</div>
              <div>Please enter a smaller amount</div>
            </div>
          )}
          <MaterialButton
            fullWidth={true}
            color="primary"
            disabled={!canDonate}
            classes={{
              root: classes.donationButton,
              disabled: classes.donationButtonDisabled,
            }}
            onClick={async () => {
              if (wantsDonationReceipt) {
                setIsDrawerOpen(true);
              } else {
                await donate();
              }
            }}
          >
            <span className={classes.donationButtonText}>Donate</span>
          </MaterialButton>
          <SwipeableDrawer
            classes={{
              paperAnchorBottom: classes.drawer,
            }}
            anchor="bottom"
            open={isDrawerOpen}
            disableDiscovery={true}
            disableSwipeToOpen={true}
            onOpen={() => {}}
            onClose={() => setIsDrawerOpen(false)}
          >
            <BottomSheet
              onClose={() => setIsDrawerOpen(false)}
              onDonate={async (email) => {
                await donate(email);
              }}
            ></BottomSheet>
          </SwipeableDrawer>
        </form>
        <Snackbar
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          open={isNetworkError}
          autoHideDuration={6000}
          onClose={() => setIsNetworkError(false)}
        >
          <SnackbarContent
            message={
              <span
                style={{
                  fontFamily: '"Colfax Regular", sans-serif',
                }}
              >
                Something went wrong. Try again later.
              </span>
            }
          ></SnackbarContent>
        </Snackbar>
      </LoadingOverlay>
    </Layout>
  );
};

export default IndexPage;
